import React, {Fragment, useState} from "react";
import {compose} from "redux";
import withStyles from "@material-ui/core/styles/withStyles";
import {withTranslation} from "react-i18next";
import Tooltip from "@material-ui/core/Tooltip";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import DimensionAttributeList from "../dimension-attribute-list";
import {getDimensionValuesAttributeList} from "../../utils/jsonStat";

const styles = theme => ({
  attributeButton: {
    padding: 8,
    cursor: "pointer"
  },
  attributeIcon: {
    height: 18,
    lineHeight: "18px",
    padding: "0 4px",
    borderRadius: 3
  },
  attributeIconAst: {
    fontSize: 18,
    fontFamily: "Do Hyeon",
    color: "rgb(136, 136, 136)"
  },
  attributeIconChar: {
    fontSize: 13,
    color: "rgb(255, 255, 255)",
    backgroundColor: "rgb(136, 136, 136)",
  }
});

const AttributeIcon = ({
                         t,
                         classes,
                         jsonStat,
                         layout,
                         dimension,
                         dimAttributeMap,
                         labelFormat,
                         iconStyle
                       }) => {

  const [isVisible, setVisibility] = useState(false);

  const dimValuesAttributeList = getDimensionValuesAttributeList(dimension, dimAttributeMap, layout);

  let ids = null;
  if (dimValuesAttributeList.length === 1) {
    ids = dimValuesAttributeList[0].ids
  }

  return (
    <Fragment>
      {dimValuesAttributeList.length > 0 && (
        <Tooltip
          title={
            <DimensionAttributeList
              jsonStat={jsonStat}
              dimension={dimension}
              dimValuesAttributeList={dimValuesAttributeList}
              labelFormat={labelFormat}
              style={{fontSize: 12}}
            />
          }
          placement="top"
        >
          <div
            className={classes.attributeButton}
            style={iconStyle}
            onClick={() => setVisibility(true)}
          >
            {(ids && ids.length === 1 && ids[0].length <= 2)
              ? (
                <div className={`${classes.attributeIcon} ${classes.attributeIconChar}`}>
                  {ids[0]}
                </div>
              )
              : (
                <div className={`${classes.attributeIcon} ${classes.attributeIconAst}`}>
                  (*)
                </div>
              )
            }
          </div>
        </Tooltip>
      )}

      <Dialog
        open={isVisible}
        onClose={() => setVisibility(false)}
      >
        <DialogTitle>
          {t("components.attributeIcons.dialog.title")}
        </DialogTitle>
        <DialogContent>
          <DimensionAttributeList
            jsonStat={jsonStat}
            dimension={dimension}
            dimValuesAttributeList={dimValuesAttributeList}
            labelFormat={labelFormat}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setVisibility(false)}>
            {t("commons.confirm.close")}
          </Button>
        </DialogActions>
      </Dialog>
    </Fragment>
  )
};

export default compose(
  withStyles(styles),
  withTranslation()
)(AttributeIcon);