export const INIT = "INIT";

export const init = (baseURL: string, supportedLanguages: string[], defaultLanguage: string, appConfig: object, dashboardConfig: object, footerHtmlStr: string) => ({
    type: INIT,
    payload: {
        baseURL,
        supportedLanguages,
        defaultLanguage,
        appConfig,
        dashboardConfig,
        footerHtmlStr
    }
});
