export const submitDatasetDownload = (
  nodeId: number,
  datasetId: string,
  datasetTitle: string,
  format: string,
  extension: string,
  zipped: boolean,
  criteria: any,
  layout: any,
  params: any,
  t: any
) => {
  let url, body, type;

  if (format === DOWNLOAD_FORMAT_EXCEL) {
    url = getDatasetDownloadCustomUrl(nodeId, format, zipped);
    body = getCustomExportRequestBody(datasetId, datasetTitle, criteria, layout, params, t);
    type = "application/vnd.ms-excel";

  } else {
    url = getDatasetDownloadUrl(nodeId, datasetId, format, zipped);
    body = getCriteriaArrayFromObject(criteria);
    type = "text/plain;charset=utf-8";
  }

  return initRequest(
    DATASET_DOWNLOAD_SUBMIT,
    url,
    RequestMethod.POST,
    body,
    t => ({
      onStart: t("scenes.dataset.actions.downloadingDataset")
    }),
    {
      fileSave: {
        name: `${datasetTitle} (${datasetId}).${extension}`,
        type: type
      }
    },
    undefined,
    (statusCode: number) => statusCode === 406,
    undefined,
    undefined,
    true
  )
};

