import React from 'react';
import {compose} from "redux";
import {withTranslation} from "react-i18next";
import {withStyles} from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import Card from "@material-ui/core/Card";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from '@material-ui/core/CardActions';
import CatalogMetadataButton from "../catalog-metadata-button";
import CatalogInfoButton from "../catalog-info-button";

const styles = () => ({
  card: {
    height: "100%",
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-between"
  }
});

const CardsGrid = ({
                     t,
                     classes,
                     list,
                     onClick
                   }) =>
  <Grid container spacing={2}>
    {list.map((c, i) =>
      <Grid key={i} item md={4} xs={12}>
        <Card className={classes.card} id={`cards-grid__card-${c.id}`}>
          <CardActionArea onClick={() => onClick(c)}>
            {c.image && (
              <CardMedia
                component="img"
                height={226}
                title={c.label}
                image={c.image}
                alt=""
              />
            )}
            <CardContent>
              <Typography variant="h5" style={{fontSize: 20}}>
                {c?.label || ""}&nbsp;
              </Typography>
            </CardContent>
          </CardActionArea>
          {(c.description || c.metadataUrl) && (
            <CardActions>
              {c.description && (
                <CatalogInfoButton
                  info={c.description}
                  label={t("components.cardsGrid.actions.description.title")}
                  iconSize="small"
                />
              )}
              {c.metadataUrl && (
                <CatalogMetadataButton
                  metadataUrl={c.metadataUrl}
                  iconSize="small"
                />
              )}
            </CardActions>
          )}
        </Card>
      </Grid>
    )}
  </Grid>;

export default compose(
  withStyles(styles),
  withTranslation()
)(CardsGrid);