import React from 'react'
import {compose} from "redux";
import {connect} from "react-redux";
import {withTranslation} from "react-i18next";
import {withStyles} from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import {getTrimmedStr} from "../../utils/formatters";

const styles = theme => ({
  root: {},
  logo: {
    height: 64,
    display: "block",
    margin: "auto",
    marginBottom: 8
  },
  name: {
    marginBottom: 24
  },
  text: {
    marginBottom: 24
  },
  version: {},
});

const mapStateToProps = state => ({
  baseURL: state.config.baseURL
});

const AppInfo = ({
                   t,
                   classes,
                   baseURL,
                   hub
                 }) =>
  <Grid container className={classes.root}>
    {hub.headerLogoURL && (
      <Grid item xs={12}>
        <img src={baseURL + hub.headerLogoURL} alt="" className={classes.logo}/>
      </Grid>
    )}
    <Grid item xs={12}>
      <Typography variant="h4" color="primary" align="center" className={classes.name}>
        {hub.name}
      </Typography>
    </Grid>
    {getTrimmedStr(t("components.appInfo.content")).length > 0 && (
      <Grid item xs={12} className={classes.text}>
        {t("components.appInfo.content")}
      </Grid>
    )}
    <Grid item xs={12} className={classes.version}>
      {t("components.appInfo.poweredBy") + ": "}<b>Istat Data Browser <i>v1.3.3</i></b>
    </Grid>
  </Grid>;

export default compose(
  withStyles(styles),
  withTranslation(),
  connect(mapStateToProps)
)(AppInfo);