import {Link, withStyles} from "@material-ui/core";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import Typography from "@material-ui/core/Typography";
import React, {Component} from "react";
import Grid from "@material-ui/core/Grid";
import UserSelect from "../user-select";
import Drawer from "@material-ui/core/Drawer";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import Divider from "@material-ui/core/Divider";
import CategoriesTree from "../categories-tree";
import {withRouter} from "react-router";
import SettingsSelect from "../settings-select";
import SearchDialog from "../search-dialog";
import ButtonSelect from "../button-select";
import {goToDatasetsSearch, goToHome, goToNode, goToNodeDashboards} from "../../links";
import HomeIcon from '@material-ui/icons/Home';
import {compose} from "redux";
import {connect} from "react-redux";
import DashboardIcon from '@material-ui/icons/Dashboard';
import {canClearServerCache, canDisplayAppSettingsForm, canDisplayNodesSettingsForm, canDisplayUsersSettingsForm, canGetQueryLog} from "../../utils/user";
import {withTranslation} from "react-i18next";
import AppLanguageSelect from "../app-language-select";
import A11ySelect from "../a11y-select";
import HelpIcon from '@material-ui/icons/Help';
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import AppInfo from "../app-info";

const styles = theme => ({
  root: {
    position: "fixed",
    width: "100%",
    zIndex: theme.zIndex.appBar
  },
  title: {
    flexGrow: 1,
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
    color: "white"
  },
  toolbar: {
    zIndex: theme.zIndex.appBar,
    padding: "0 16px"
  },
  container: {
    display: "inline-block",
    verticalAlign: "middle",
  },
  drawer: {
    minWidth: 448
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0, 1),
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
  },
  drawerHeaderTitle: {
    position: "absolute",
    left: theme.spacing(2),
  },
  drawerCategories: {
    margin: theme.spacing(2)
  },
  drawerCategoriesTitle: {
    marginBottom: theme.spacing(2)
  },
  goToHome: {
    margin: theme.spacing(2, 2, 0, 2)
  },
  hubLogo: {
    height: 32,
    transform: "translateY(3px)"
  },
  hubName: {
    maxWidth: 320
  },
  hubNameText: {
    fontSize: "1.25rem",
    fontWeight: 500,
    lineHeight: 1.6,
    letterSpacing: "0.0075em",
    whiteSpace: "nowrap",
    textOverflow: "ellipsis"
  }
});


const mapStateToProps = state => ({
  baseURL: state.config.baseURL,
  user: state.user,
  isA11y: state.app.isA11y
});

class NodeHeader extends Component {

  constructor(props) {
    super(props);
    this.state = {
      isDrawerOpen: this.props.defaultTreeOpen || false,
      isInfoOpen: false
    };
    this.handleStyle = this.handleStyle.bind(this);
  }

  handleStyle = () => {
    const hubInfoWidth = (
      (document.getElementById("hub-logo")?.offsetWidth || 0) +
      (document.getElementById("hub-name")?.offsetWidth || 0)
    );

    if (document.getElementById("node-header__left-container")) {
      document.getElementById("node-header__left-container").setAttribute("style", `width: calc(50% - ${hubInfoWidth / 2}px)`);
    }

    if (document.getElementById("node-header__center-container")) {
      document.getElementById("node-header__center-container").setAttribute("style", `width: ${hubInfoWidth}px`);
    }

    if (document.getElementById("node-header__right-container")) {
      document.getElementById("node-header__right-container").setAttribute("style", `width: calc(50% - ${hubInfoWidth / 2}px)`);
    }
  }

  componentDidMount() {
    this.handleStyle();
    window.addEventListener("resize", this.handleStyle);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.handleStyle);
  }

  componentDidUpdate() {
    this.handleStyle();
  }

  onDrawerOpen = () => {
    this.setState({
      isDrawerOpen: true
    });
  };

  onDrawerClose = () => {
    this.setState({
      isDrawerOpen: false
    });
  };

  onInfoOpen = () => {
    this.setState({
      isInfoOpen: true
    });
  };

  onInfoClose = () => {
    this.setState({
      isInfoOpen: false
    });
  };

  render() {

    const {
      classes,
      selectedCategoryPath,
      title,
      defaultAppConfigOpen,
      defaultUsersConfigOpen,
      defaultNodesConfigOpen,
      defaultNodeConfigOpen,
      nodes,
      noNode,
      catalog,
      node,
      query,
      hub,
      baseURL,
      user,
      isA11y,
      isDefault,
      getCustomA11yPath,
      getAdditionalA11yUrlParams,
      t
    } = this.props;

    const {
      isDrawerOpen,
      isInfoOpen
    } = this.state;

    const isDefaultUniqueNode =
      !noNode && isDefault && node &&
      nodes.filter(n => n.code.toLowerCase() !== node.code.toLowerCase()).length === 0;

    return (
      <div className={classes.root} id="node-header" role="navigation" aria-label={t("ariaLabels.header.header")}>
        <AppBar position="static" color="primary">
          <div>
            <div id="node-header__left-container" className={classes.container}>
              {!noNode && (
                <Toolbar className={classes.toolbar}>
                  <Grid container justify="flex-start" spacing={1} alignItems="center">
                    <Grid item id="drawer-btn">
                      <IconButton
                        className={classes.menuButton}
                        edge="start"
                        color="inherit"
                        onClick={this.onDrawerOpen}
                      >
                        <MenuIcon/>
                      </IconButton>
                    </Grid>
                    <Grid item id="home-btn">
                      <IconButton
                        className={classes.homeButton}
                        edge="start"
                        color="inherit"
                        onClick={() => goToNode((node?.code || "").toLowerCase())}
                      >
                        <HomeIcon/>
                      </IconButton>
                    </Grid>
                    {!isDefaultUniqueNode && (
                      <Grid item id="node-selector" style={{width: `calc(100% - 88px)`}}>
                        <ButtonSelect
                          value={
                            <Typography variant="h6" color="inherit" style={{textTransform: "none"}} noWrap>
                              {node?.name || node?.code}
                            </Typography>
                          }
                          onChange={code => goToNode(code.toLowerCase())}
                          tooltip={node?.name}
                          buttonStyle={{maxWidth: "100%"}}
                        >
                          {nodes.sort((a, b) => a.order - b.order).map(({code, name}, i) =>
                            <span key={i} data-value={code}>
                              {name || code}
                            </span>
                          )}
                        </ButtonSelect>
                      </Grid>
                    )}
                  </Grid>
                </Toolbar>
              )}
            </div>
            <div id="node-header__center-container" className={classes.container}>
              <Link onClick={goToHome} style={{cursor: "pointer"}}>
                <Grid container spacing={2} alignItems="center" justify="center">
                  {hub.headerLogoURL && (
                    <Grid item id="hub-logo">
                      <img src={baseURL + hub.headerLogoURL} alt="" className={classes.hubLogo} onLoad={this.handleStyle}/>
                    </Grid>
                  )}
                  <Grid item id="hub-name" className={classes.hubName}>
                    <Typography variant="h1" color="secondary" className={classes.hubNameText} noWrap>
                      {hub.name}
                    </Typography>
                  </Grid>
                </Grid>
              </Link>
            </div>
            <div id="node-header__right-container" className={classes.container}>
              <Toolbar className={classes.toolbar}>
                <Grid container justify="flex-end" spacing={1} alignItems="center">
                  {!noNode && (
                    <Grid item id="search-btn">
                      <SearchDialog
                        query={query}
                        modalWidth={320}
                        dialogTop={56}
                        onSubmit={value => goToDatasetsSearch(node.code, value)}
                      />
                    </Grid>
                  )}
                  <Grid item id="language-btn">
                    <AppLanguageSelect/>
                  </Grid>
                  <Grid item id="a11y-btn">
                    <A11ySelect
                      getAdditionalA11yUrlParams={getAdditionalA11yUrlParams}
                      getCustomA11yPath={getCustomA11yPath}
                    />
                  </Grid>
                  {!noNode && !isA11y && node && node.dashboards && node.dashboards.length > 0 && (
                    <Grid item id="dashboards-btn">
                      <IconButton
                        onClick={() => goToNodeDashboards(node.code)}
                        color="inherit"
                        aria-label={t("ariaLabels.header.dashboard")}
                      >
                        <DashboardIcon/>
                      </IconButton>
                    </Grid>
                  )}
                  {(canDisplayAppSettingsForm(user) ||
                    canDisplayUsersSettingsForm(user) ||
                    canDisplayNodesSettingsForm(user) ||
                    canGetQueryLog(user) ||
                    canClearServerCache(user)
                  ) && (
                    <Grid item id="admin-settings-btn">
                      <SettingsSelect
                        defaultAppOpen={defaultAppConfigOpen}
                        defaultNodesOpen={defaultNodesConfigOpen}
                        defaultUsersOpen={defaultUsersConfigOpen}
                        defaultNodeOpen={defaultNodeConfigOpen}
                        nodes={nodes}
                      />
                    </Grid>
                  )}
                  <Grid item id="user-settings-btn">
                    <UserSelect nodes={nodes}/>
                  </Grid>
                  <Grid item id="info-btn">
                    <IconButton
                      onClick={this.onInfoOpen}
                      color="inherit"
                      aria-label={t("ariaLabels.header.info")}
                    >
                      <HelpIcon/>
                    </IconButton>
                  </Grid>
                </Grid>
              </Toolbar>
            </div>
          </div>
        </AppBar>
        {!noNode && (
          <Drawer
            anchor="left"
            open={isDrawerOpen}
            classes={{
              paper: classes.drawer
            }}
            onClose={this.onDrawerClose}
          >
            <div className={classes.drawerHeader}>
              <Typography variant="h6" className={classes.drawerHeaderTitle}>
                {title || node?.code}
              </Typography>
              <IconButton onClick={this.onDrawerClose}>
                <ChevronLeftIcon/>
              </IconButton>
            </div>
            <Divider/>
            <div className={classes.goToHome}>
              <Link underline="none" onClick={() => goToNode(node.code)} style={{cursor: "pointer"}}>
                <Grid container spacing={1}>
                  <Grid item>
                    <HomeIcon/>
                  </Grid>
                  <Grid item>
                    <Typography>
                      {t("components.header.drawer.backToNodeHome")}
                    </Typography>
                  </Grid>
                </Grid>
              </Link>
            </div>
            {catalog && (
              <div className={classes.drawerCategories}>
                <div className={classes.drawerCategoriesTitle}>
                  <Typography>
                    {t("components.header.drawer.categoriesTitle")}
                  </Typography>
                </div>
                <CategoriesTree
                  selectedCategoryPath={selectedCategoryPath}
                  catalog={catalog}
                  node={node}
                  onClose={this.onDrawerClose}
                />
              </div>
            )}
          </Drawer>
        )}
        <Dialog
          open={isInfoOpen}
          maxWidth="sm"
          onClose={this.onInfoClose}
        >
          <DialogContent>
            <AppInfo hub={hub}/>
          </DialogContent>
          <DialogActions>
            <Button onClick={this.onInfoClose}>
              {t("commons.confirm.close")}
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }
}

export default compose(
  withStyles(styles),
  withTranslation(),
  connect(mapStateToProps),
  withRouter
)(NodeHeader);