import React, {Fragment, useState} from "react";
import Typography from "@material-ui/core/Typography";
import CardActionArea from "@material-ui/core/CardActionArea";
import Grid from "@material-ui/core/Grid";
import Chip from "@material-ui/core/Chip";
import IconButton from "@material-ui/core/IconButton";
import ExpandLessIcon from "@material-ui/icons/ExpandLess";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import Avatar from "@material-ui/core/Avatar";
import DialogContent from "@material-ui/core/DialogContent";
import Dialog from "@material-ui/core/Dialog";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import {useTranslation} from "react-i18next";
import getDownloadButtonStyle from './getDownloadButtonStyle';
import withStyles from "@material-ui/core/styles/withStyles";
import SanitizedHTML from "../sanitized-html";
import CatalogInfoButton from "../catalog-info-button";
import CatalogMetadataButton from "../catalog-metadata-button";
import Tooltip from "@material-ui/core/Tooltip";
import InfoIcon from '@material-ui/icons/Info';

const styles = () => ({
  card: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "flex-start"
  },
  collapsedDescription: {
    height: 48,
    overflow: "hidden"
  },
  expandedDescription: {
    minHeight: 48
  },
  resultSource: {
    fontSize: 14,
    color: "rgba(0, 0, 0, 0.54)",
  },
  cardContent: {
    width: "100%",
    padding: "16px !important"
  },
  cardActions: {
    padding: "16px 8px",
    "& button": {
      padding: 4
    }
  },
  description: {
    "& > p": {
      margin: 0
    }
  },
  downloadChipDeleteIcon: {
    backgroundColor: "white"
  },
  counterAvatar: {
    width: 16,
    height: 16,
    marginLeft: -4,
    marginRight: 4
  },
  chip: {
    "& > span": {
      whiteSpace: "pre-wrap"
    }
  }
});

const DatasetCard = ({
                       dataset,
                       classes,
                       isExpanded,
                       onViewDataset,
                       onExpand,
                       onCollapse,
                       xs,
                       md,
                       isMultiCategorized
                     }) => {

  const {t} = useTranslation();

  const [isDownloadModalOpen, setIsDownloadModalOpen] = useState(false);
  const [downloadModal, setDownloadModal] = useState(null);

  const groupedAttachments = [];
  (dataset.attachedDataFiles || []).forEach(({format, url}) => {

    const group = groupedAttachments.find(({extension}) => extension === format.toLowerCase());

    if (!group) {
      groupedAttachments.push({
        extension: format.toLowerCase(),
        urls: [url]
      })
    } else {
      group.urls.push(url);
    }
  });

  const headerContent = (
    <CardContent className={classes.cardContent}>
      <Grid container justify="space-between" spacing={2} alignItems="flex-start">
        <Grid item>
          <Grid container direction="column">
            <Grid item>
              <Grid container spacing={1} alignItems="center">
                <Grid item>
                  <Typography variant="h6">
                    {dataset.title}
                  </Typography>
                </Grid>
                {isMultiCategorized && (
                  <Grid item>
                    <Tooltip title={t("components.datasetCard.isMultiCategorized.title")}>
                      <InfoIcon fontSize="small"/>
                    </Tooltip>
                  </Grid>
                )}
              </Grid>
            </Grid>
            {dataset.source && (
              <Grid item>
                <Typography variant="body1" className={classes.resultSource}>
                  {dataset.source}
                </Typography>
              </Grid>
            )}
          </Grid>
        </Grid>
        {groupedAttachments.length > 0 && (
          <Grid item>
            <Grid container spacing={1} style={{margin: 0}}>
              {groupedAttachments
                .map((group, index) =>
                  <Grid item key={index}>
                    <Chip
                      className={`download-btn--${group.extension.toLowerCase()}`}
                      label={group.extension.toUpperCase()}
                      deleteIcon={
                        group.urls.length > 1
                          ? (
                            <Avatar style={{fontSize: 12, color: "black"}}
                                    classes={{root: classes.counterAvatar}}>
                              {group.urls.length}
                            </Avatar>
                          )
                          : null
                      }
                      onDelete={
                        group.urls.length > 1
                          ? e => {
                            e.stopPropagation();
                            setIsDownloadModalOpen(true);
                            setDownloadModal(group)
                          }
                          : null
                      }
                      size="small"
                      onClick={
                        e => {
                          e.stopPropagation();
                          if (group.urls.length > 1) {
                            setIsDownloadModalOpen(true);
                            setDownloadModal(group)
                          } else {
                            window.open(group.urls[0], "_blank")
                          }
                        }
                      }
                      style={{
                        cursor: "pointer",
                        ...getDownloadButtonStyle(group.extension)
                      }}
                      classes={{
                        deleteIconSmall: classes.downloadChipDeleteIcon
                      }}
                      aria-label={
                        t("scenes.results.datasetCard.downloadAria", {
                          title: dataset.title,
                          extension: group.extension.toUpperCase()
                        })
                      }
                    />
                  </Grid>
                )}
            </Grid>
          </Grid>
        )}
      </Grid>
    </CardContent>
  );

  const header =
    dataset.catalogType !== "ONLY_FILE"
      ? (
        <CardActionArea
          onClick={onViewDataset}
        >
          {headerContent}
        </CardActionArea>
      )
      : headerContent;

  const description =
    dataset.description && (
      <Typography
        component='div'
        variant="body1"
        className={isExpanded ? classes.expandedDescription : classes.collapsedDescription}
      >
        {dataset.description && <SanitizedHTML html={dataset.description} className={classes.description}/>}
      </Typography>
    );

  const expandIcon = <Grid container justify="flex-end">
    <Grid item>
      <IconButton
        onClick={isExpanded ? onCollapse : onExpand}
      >
        {isExpanded
          ? <ExpandLessIcon/>
          : <ExpandMoreIcon/>
        }
      </IconButton>
    </Grid>
  </Grid>;

  return (
    <Fragment>
      <Grid item xs={xs} md={md} id={encodeURIComponent(dataset.identifier)}>
        <Card>
          <div className={classes.card} style={{width: "100%"}}>
            {header}
            {(dataset.note || dataset.referenceMetadata) && (
              <CardActions className={classes.cardActions}>
                {dataset.note && (
                  <CatalogInfoButton
                    info={dataset.note}
                    label={t("components.datasetCard.actions.notes.title")}
                    iconSize="small"
                  />
                )}
                {dataset.referenceMetadata && (
                  <CatalogMetadataButton
                    metadataUrl={dataset.referenceMetadata}
                    iconSize="small"
                  />
                )}
              </CardActions>
            )}
          </div>
          {description && (
            <CardContent className={classes.cardContent}>
              {description}
              {description && expandIcon}
            </CardContent>
          )}
        </Card>
      </Grid>

      <Dialog
        open={!!isDownloadModalOpen}
        disableEscapeKeyDown
        disableBackdropClick
        onClose={() => setIsDownloadModalOpen(false)}
        onExited={() => setDownloadModal(null)}
      >
        <DialogContent>
          <List>
            {downloadModal?.urls.map((url, key) =>
              <ListItem key={key}>
                <a href={url} target="_blank" rel="noopener noreferrer">{url}</a>
              </ListItem>
            )}
          </List>
          <DialogActions>
            <Button onClick={() => setIsDownloadModalOpen(false)}>
              {t("commons.confirm.close")}
            </Button>
          </DialogActions>
        </DialogContent>
      </Dialog>

    </Fragment>
  );
};

export default withStyles(styles)(DatasetCard);