import React, {Component, Fragment} from 'react';
import Box from "@material-ui/core/Box";
import Hero from "../hero";
import CardsGrid from "../cards-grid";
import PageSection from "../page-section";
import Button from "@material-ui/core/Button";
import Page from "../page";
import NodeHeader from "../node-header";
import withStyles from "@material-ui/core/styles/withStyles";
import Container from "@material-ui/core/Container";
import {goToHubDashboards, goToNode} from "../../links";
import {compose} from "redux";
import {connect} from "react-redux";
import Divider from "@material-ui/core/Divider";
import {withTranslation} from "react-i18next";
import Footer from "../footer";
import SanitizedHTML from "../sanitized-html";
import {HashLink} from 'react-router-hash-link';
import {withRouter} from "react-router";
import {getPageTitle} from "../../utils/other";

const styles = theme => ({
  appBar: {
    transition: "opacity 1s",
  },
  toolbarButtons: {
    marginLeft: "auto",
    '& > *': {
      marginRight: theme.spacing(1)
    }
  },
  anchorContainer: {
    position: "relative"
  },
  anchor: {
    position: "absolute",
    top: -theme.mixins.toolbar.minHeight
  },
  fullWidthContainer: {
    backgroundColor: "#f5f5f5",
    width: "100%"
  },
  container: {
    paddingTop: theme.spacing(3)
  },
  section: {
    paddingTop: theme.spacing(3)
  },
  footer: {
    paddingTop: theme.spacing(4)
  },
  footerContent: {
    paddingTop: theme.spacing(2)
  },
  nodesContainer: {
    marginTop: theme.spacing(2)
  },
  centerContainer: {
    width: 316,
    textAlign: "center"
  },
  sistanLogo: {
    width: 32,
    height: 32,
    transform: "translateY(3px)"
  },
  leftContainer: {
    width: "calc((100% - 316px)/2)"
  },
  rightContainer: {
    width: "calc((100% - 316px)/2)"
  },
  defaultBackground: {
    backgroundColor: theme.palette.primary.light
  }
});

const mapStateToProps = state => ({
  hub: state.hub,
  baseURL: state.config.baseURL,
  isA11y: state.app.isA11y
});

class Home extends Component {

  info = React.createRef();
  nodesRef = React.createRef();

  state = {
    isListMode: true,
  };

  scroll(ref) {
    ref.current.scrollIntoView({
      behavior: 'smooth',
      alignToTop: true,
    });
  }

  /*  onListModeToggle = () => {
      this.setState({isListMode: !this.state.isListMode});
    };*/

  render() {

    const {classes, hub, baseURL, isA11y, t, location} = this.props;
    // const {isListMode} = this.state;

    const params = new URLSearchParams(location.search);
    params.set("accessible", "true");
    const paramsStr = params.toString();
    const path = location.pathname;

    return (
      <Fragment>
        <p id="skip-link">
          {!isA11y && (
            <a
              href={"./#" + path + (paramsStr.length > 0 ? "?" : "") + paramsStr}
              target="_self"
              className="element_invisible element_focusable"
            >
              {t("commons.hashLinks.accessible")}
            </a>
          )}
          <HashLink to={{hash: "#main", search: location.search}} className="element_invisible element_focusable">
            {t("commons.hashLinks.main")}
          </HashLink>
          <HashLink to={{hash: "#footer", search: location.search}} className="element_invisible element_focusable">
            {t("commons.hashLinks.footer")}
          </HashLink>
        </p>
        <Page id="landing-page" title={getPageTitle([hub?.hub?.name], t)}>
          <NodeHeader
            noNode
            defaultNodeConfigOpen={this.props.defaultNodeConfigOpen}
            defaultAppConfigOpen={this.props.defaultAppConfigOpen}
            defaultUserConfigOpen={this.props.defaultUserConfigOpen}
            defaultNodesConfigOpen={this.props.defaultNodesConfigOpen}
            hub={hub.hub}
            nodes={hub.nodes}
          />
          <Hero
            id="main"
            title={hub.hub.name}
            slogan={hub.hub.slogan}
            logo={hub.hub.logoURL && <img src={baseURL + hub.hub.logoURL} alt=""/>}
            background={
              hub.hub.backgroundMediaURL
                ? (hub.hub.backgroundMediaURL.match(/\.(jpeg|jpg|gif|png|JPEG|JPG|GIF|PNG|svg|SVG)$/)
                  ? <img src={baseURL + hub.hub.backgroundMediaURL} alt=""/>
                  : (
                    <video autoPlay muted loop>
                      <source src={baseURL + hub.hub.backgroundMediaURL}/>
                    </video>
                  )
                )
                : <div className={classes.defaultBackground}/>
            }
          >
            {hub.hub.description && hub.hub.description.length > 0 && (
              <Button
                size="large"
                color="secondary"
                variant="contained"
                onClick={() => {
                  this.scroll(this.info);
                  this.info.current.focus({preventScroll: true});
                }}
              >
                {t("scenes.hub.informations")}
              </Button>
            )}
            <Button
              size="large"
              color="secondary"
              variant="contained"
              onClick={() => {
                this.scroll(this.nodesRef);
                this.nodesRef.current.focus({preventScroll: true});
              }}
            >
              {t("scenes.hub.nodes")}
            </Button>
            {!isA11y && hub.hub.dashboards && hub.hub.dashboards.length > 0 && (
              <Button size="large" color="secondary" variant="contained" onClick={goToHubDashboards}>
                {t("scenes.hub.dashboards")}
              </Button>
            )}
          </Hero>
          <div className={classes.fullWidthContainer}>
            <Container className={classes.container}>
              {hub.hub.description && hub.hub.description.length > 0 && (
                <div className={classes.anchorContainer}>
                  <div className={classes.anchor} ref={this.info} tabIndex={-1}/>
                  <PageSection
                    className={`hub-information ${classes.section}`}
                    sectiontitle={t("scenes.hub.informations")}
                  >
                    <Box textAlign="justify">
                      <SanitizedHTML html={hub.hub.description} allowTarget/>
                    </Box>
                  </PageSection>
                </div>
              )}
              <div className={classes.anchorContainer}>
                <div className={classes.anchor} ref={this.nodesRef} tabIndex={-1}/>
                <PageSection className={classes.section} sectiontitle={t("scenes.hub.nodes")}>
                  <Box className={classes.nodesContainer}>
                    <CardsGrid
                      list={hub.nodes.sort((a, b) => a.order - b.order).map(({code, name, backgroundMediaURL}) => ({
                        code,
                        id: code,
                        label: name,
                        image: backgroundMediaURL ? baseURL + backgroundMediaURL : "./images/default-node-background.jpg"
                      }))}
                      onClick={node => goToNode(node.code.toLowerCase())}
                    />
                  </Box>
                </PageSection>
              </div>
              <footer className={classes.footer} id="footer">
                <Divider/>
                <div className={classes.footerContent}>
                  <Footer/>
                </div>
              </footer>
            </Container>
          </div>
        </Page>
      </Fragment>
    );
  }
}

export default compose(
  withStyles(styles),
  withTranslation(),
  connect(mapStateToProps),
  withRouter
)(Home);