import React from 'react';
import {getAttributeLabel, getAttributeValueLabel, getDimensionValueLabelFromJsonStat} from "../../utils/jsonStat";
import Grid from "@material-ui/core/Grid";

const DimensionAttributeList = ({
                                  jsonStat,
                                  dimension,
                                  dimValuesAttributeList,
                                  labelFormat,
                                  style
                                }) =>
  <div style={{...style}}>
    <Grid container spacing={2}>
      {(dimValuesAttributeList || []).map(({dimVal, attributes}) => (
        <Grid item key={dimVal} xs={12}>
          <Grid container spacing={1}>
            {dimension && (
              <Grid item key={dimVal} xs={12}>
                <b><u>{getDimensionValueLabelFromJsonStat(jsonStat, dimension, dimVal, labelFormat) + ":"}</u></b>
              </Grid>
            )}
            {(attributes || []).map((attribute, idx) => (
              <Grid item key={dimVal + idx} xs={12}>
                - <b>{getAttributeLabel(attribute, labelFormat)}</b>: {getAttributeValueLabel(attribute, labelFormat)}
              </Grid>
            ))}
          </Grid>
        </Grid>
      ))}
    </Grid>
  </div>;

export default DimensionAttributeList;