import React, {Fragment, useState} from "react";
import {compose} from "redux";
import {withTranslation} from "react-i18next";
import withStyles from "@material-ui/core/styles/withStyles";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import FullscreenDialog from "../fullscreen-dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";

const styles = theme => ({
  metadataIcon: {
    width: 24,
    height: 24,
    lineHeight: "24px",
    fontSize: 20
  }
});

const CatalogMetadataButton = ({
                                 t,
                                 classes,
                                 metadataUrl,
                                 label,
                                 iconSize
                               }) => {

  const [isVisible, setVisibility] = useState(false);

  const onOpen = () => {
    setVisibility(true);
  };

  const onClose = ev => {
    ev.stopPropagation();
    setVisibility(false);
  };

  if (!metadataUrl) {
    return <span/>;
  }

  return (
    <Fragment>
      <Tooltip title={label || t("components.catalogMetadataButton.label")}>
        <IconButton
          size={iconSize}
          onClick={ev => {
            ev.stopPropagation();
            onOpen();
          }}
          aria-label={label || t("components.catalogMetadataButton.label")}
        >
          <b className={classes.metadataIcon}>M</b>
        </IconButton>
      </Tooltip>

      <FullscreenDialog
        open={isVisible}
        onClose={onClose}
      >
        <DialogTitle>
          {label || t("components.catalogMetadataButton.label")}
        </DialogTitle>
        <DialogContent>
          <iframe
            title={"title"}
            src={metadataUrl}
            style={{
              border: 0,
              width: "100%",
              height: "calc(100% - 6px)"
            }}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={onClose}>
            {t("commons.confirm.close")}
          </Button>
        </DialogActions>
      </FullscreenDialog>

    </Fragment>
  );
}

export default compose(
  withStyles(styles),
  withTranslation()
)(CatalogMetadataButton);